export const keyboardState = { 
    accelerate: false,    //
    brake: false,         //
    steerLeft: false,     //
    steerRight: false,    //
    emergencyBrake: false //
};
export const touchState = {
    accelerate: false,
    brake: false,
    steerLeft: false,
    steerRight: false,
    emergencyBrake: false
};

// 
const W = ['w', 'arrowup'];
const A = ['a', 'arrowleft']; 
const S = ['s', 'arrowdown'];
const D = ['d', 'arrowright'];
const R = ['r'];
const SPACE = [' '];
export { W, A, S, D, R, SPACE };

export class InputHandler {
    constructor() {
        this.keyboardState = keyboardState;
        this.touchState = touchState;

        this.keysPressed = {};       //
        this.accelerate = false;     //
        this.brake = false;          //
        this.steerLeft = false;      //
        this.steerRight = false;     //
        this.emergencyBrake = false; //
        this.reset = false;          //

        this.touchStart = { x: 0, y: 0 }; //joystick ()
        this.touching = false;
        this.touchThreshold = 30;    //

        document.addEventListener('keydown', (event) => this.updateKey(event, true), false);
        document.addEventListener('keyup', (event) => this.updateKey(event, false), false);

        document.addEventListener('touchstart', this.onTouchStart.bind(this), { passive: false });
        document.addEventListener('touchmove', this.onTouchMove.bind(this), { passive: false });
        document.addEventListener('touchend', this.onTouchEnd.bind(this), { passive: false });
    }
    updateKey(event, isPressed) {
        const key = event.key.toLowerCase();
        this.keysPressed[key] = isPressed;
    }
    isPressed(keyArray) {
        return keyArray.some(key => this.keysPressed[key]);
    }

    updateKeyboardState() {/*   */
        this.keyboardState.accelerate = this.isPressed(W);          //
        this.keyboardState.brake = this.isPressed(S);               // or ?
        this.keyboardState.steerLeft = this.isPressed(A);           //
        this.keyboardState.steerRight = this.isPressed(D);          //
        this.keyboardState.emergencyBrake = this.isPressed(SPACE);  //
        this.keyboardState.reset = this.isPressed(R);               //0
    }

    onTouchStart(event) {
        const touch = event.touches[0];
        this.touchStart.x = touch.clientX;
        this.touchStart.y = touch.clientY;
        this.touching = true;
    }

    onTouchMove(event) {
        if (!this.touching) return;

        const touch = event.touches[0];
        const dx = touch.clientX - this.touchStart.x;
        const dy = touch.clientY - this.touchStart.y;

        this.touchState.accelerate = Math.abs(dy) > this.touchThreshold && dy < 0;
        this.touchState.brake = Math.abs(dy) > this.touchThreshold && dy > 0;
        this.touchState.steerLeft = Math.abs(dx) > this.touchThreshold && dx < 0;
        this.touchState.steerRight = Math.abs(dx) > this.touchThreshold && dx > 0;
    }

    onTouchEnd(event) {
        this.touching = false;
        // 
        this.touchState.accelerate = false;
        this.touchState.brake = false;
        this.touchState.steerLeft = false;
        this.touchState.steerRight = false;
    }
}
